import React from 'react';
import { Element } from 'react-scroll';
import '../styles/About.css';

const About = (props) => (
  <Element name="about-element">
    <section className="about-section">
      <h1 className="about-header">About Me</h1>
      <img src="./images/beardJarrod.jpg" className="round-border about-pic" alt="Jarrod Powell" />
      <div className="about-summary">
        <p>I am a full-stack software engineer who loves to tackle tough programming challenges with passion, determination
        and creativity. Other than programming, I also enjoy composing and producing music, 
         playing the viola and guitar, and playing basketball and baseball/softball.
        </p>
        <p>Feel free to message me on <a href="https://www.linkedin.com/in/jarrod-powell">LinkedIn</a>. 
         If you're interested, you can also listen to some of my music on <a href="https://soundcloud.com/jpowell1032">SoundCloud.</a></p> 
      </div>
    </section>
  </Element>
  )

export default About;


